* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 22px;
}

body {
    min-height: 100vh;
    font-family: 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    max-width: 20000px;
    border: 1px solid mediumblue;
    margin: auto;
}

header {
    width: 100%;
    padding: 0 0.25em;
    background-color: mediumblue;
    color: aliceblue;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

main {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: flex-start;
    align-items: center;
    overflow-y: auto;
}

ul {
    width: 100%;
    list-style: none;
    padding: 0 0.25rem 0.25rem;
}

ul li::before {
    content: "\200B";
}

.item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5rem 0 0.5rem 0.5rem;
    margin: 0.25rem 0;
    background-color: #eee;
}

.item:first-child {
    margin: 0;
}

.item input[type="checkbox"] {
    text-align: center;
    width: 2.5rem;
    width: 48px;
    min-width: 48px;
    height: 2.5rem;
    height: 48px;
    min-height: 48px;
    cursor: pointer;
    margin-right: 0.5rem;
    background-color: blue;
}

.item input[type="checkbox"]:focus+label {
    text-decoration: underline;
}

.item>label {
    font-size: 0.75rem;
    flex-grow: 1;
}

.item svg {
    width: 48px;
    min-width: 48px;
    height: 36px;
    font-size: 1rem;
    color: steelblue;
    cursor: pointer;
}

.item svg:focus,
.item svg:hover {
    color: red;
    outline: none;
}

.addForm {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin: 25px 50px 25px 25px;
    padding: 25px 50px 25px 25px;
    border-bottom: 2px solid #eee;
}

.addForm label {
    position: absolute;
    left: -99999px;
}

.addForm input[type='text'] {
    flex-grow: 1;
    width: 100%;
    height: 1000%;
    font-size: 1rem;
    padding: 50px 50px;
    border-radius: 0.25rem;
    margin-right: 50px;
    outline: none;
}

button {
    height: 10px;
    min-width: 20px;
    border-radius: 0.25rem;
    padding: 25px 25px 25px;
    font-size: 1rem;
    background-color: aliceblue;
    color: mediumblue;
    cursor: pointer;
}

button:focus,
button:hover {
    color: white;
    background-color: limegreen;
    outline: none;
}

.searchForm {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin: 0.25rem 0 0;
    padding: 0 0.5rem 0.25rem;
    border-bottom: 1px solid #eee;
}

.searchForm label {
    position: absolute;
    left: -99999px;
}

.searchForm input[type='text'] {
    flex-grow: 1;
    max-width: 100%;
    min-height: 48px;
    font-size: 1rem;
    padding: 0.25rem;
    border-radius: 0.25rem;
    outline: none;
}

footer {
    width: 100%;
    padding: 0.25em;
    background-color: mediumblue;
    color: aliceblue;
    display: grid;
    place-content: center;
}